import { useContext, useState, useEffect } from 'react'
import { useFormik } from 'formik'
import * as yup from 'yup'
import axios from 'axios'
import ApiHeader from '../../../Components/ApiList/ApiHeader'
import ApiHeader2 from '../../../Components/ApiList/ApiHeader2'
import apiLinks from '../../../Components/ApiList/ObjectionRectificationApi'
import 'animate.css'
import { toast, ToastContainer } from 'react-toastify'
import { useNavigate, useParams } from 'react-router-dom'
import { FcDepartment, FcDocument } from 'react-icons/fc'
import { ColorRing } from 'react-loader-spinner'
import './assets/fonts/Font.css'
import { RiBuilding2Fill } from 'react-icons/ri'
import { BsPlusCircle } from 'react-icons/bs'
import { TbWebhook } from 'react-icons/tb'
import { FiMinusCircle } from 'react-icons/fi'
import BarLoader from '../../../Components/Common/BarLoader'
import BottomErrorCard from '../../../Components/BottomErrorCard'
import PropertyApiList from '../../../Components/ApiList/PropertyApiList'
import { allowCharacterNumberInput, allowCharacterSpaceCommaInput, allowMailInput, allowNumberInput, getCurrentDate, nullToNA } from '../../../Components/Common/PowerUps/PowerupFunctions'
import { FaUserNurse } from 'react-icons/fa'
import { BiAddToQueue } from 'react-icons/bi'
import { RiDeleteBack2Line } from 'react-icons/ri'
import { TbEdit } from 'react-icons/tb'
import { TiDelete } from 'react-icons/ti'
import { AiFillInfoCircle } from 'react-icons/ai'
import { contextVar } from '../../../Components/ContextVar'

const ObjectionForgery = (props) => {

  const { getForgeryReason, getForgeryDoc, postForgery } = apiLinks()
  const { api_getPropHoldingDetailsById } = PropertyApiList()

  const [documentUpload, setdocumentUpload] = useState()
  const [isLoader, setisLoader] = useState(false)
  const [noDoc, setnoDoc] = useState(1)
  const [documentForgery, setdocumentForgery] = useState(null)
  const [erroState, seterroState] = useState(false);
  const [erroMessage, seterroMessage] = useState(null);
  const [forgeryReason, setforgeryReason] = useState(null)
  const [propertyData, setpropertyData] = useState(null)
  const [evidence, setEvidence] = useState([])

  let inputStyle =
    "border-2 border-slate-300 focus:border-2 focus:border-blue-400 rounded-md poppins 2xl:text-sm text-xs px-4 py-1 w-full";

  const reasonList = [
    "Instead of the owner of the building, the tenant or other occupy the holding in his name.",
    "After the death of the owner of the holding registered in the corporation requisition register, only one shareholder obtained the holding number in his name.",
    "Application has been made for establishment of holding by more than one claimant for the same building."
  ]

  // Getting Forgery Data
  // ================================
  useEffect(() => {

    setisLoader(true)

    axios.post(api_getPropHoldingDetailsById, { propertyId: props?.id }, ApiHeader())
      .then((res) => {
        if (res?.data?.status) {
          // console.log('Getting property data => ', res)
          setpropertyData(res?.data?.data)
        } else {
          // console.log('Getting property data false => ', res)
        }
      })
      .catch((err) => {
        // console.log('Getting property data error => ', err)
      })
      .finally(() => setisLoader(false))

    axios.post(getForgeryReason, {}, ApiHeader())
      .then((res) => {
        if (res?.data?.status) {
          // console.log('Getting forgery data => ', res)
          setforgeryReason(res?.data?.data)
        } else {
          // console.log('Getting forgery data false => ', res)
        }
      })
      .catch((err) => {
        // console.log('Getting forgery data error => ', err)
      })
      .finally(() => setisLoader(false))
  }, [props?.id])

  // Get Document code 
  // ================================
  const getDocCode = (docId) => {
    setisLoader(true)
    setEvidence([])
    axios.post(getForgeryDoc, { forgeryTypeMstrId: docId }, ApiHeader())
      .then((res) => {
        if (res?.data?.status) {
          console.log('Getting forgery document => ', res)
          setdocumentForgery(res?.data?.data)
          res?.data?.data?.map((elem, index) => {
            setEvidence(prev => [...prev, { [elem?.docName]: null, docCode: '' }])
          })
        } else {
          console.log('Getting forgery document false => ', res)
        }
      })
      .catch((err) => {
        console.log('Getting forgery document error => ', err)
      })
      .finally(() => setisLoader(false))
  }

  const documentCodeChange = (e, name) => {
    console.log("document code on change => ", e.target.value)
    setEvidence(prevState => {
      const updatedEvidence = [...prevState];
      const objIndex = updatedEvidence.findIndex(obj => obj[name] !== undefined);
      if (objIndex !== -1) {
        updatedEvidence[objIndex] = {
          ...updatedEvidence[objIndex],
          docCode: e.target.value

        };
      }
      return updatedEvidence;
    });
  }
  const documentChange = (e, name) => {
    console.log("document file on change => ", e.target.files[0])
    setEvidence(prevState => {
      const updatedEvidence = [...prevState];
      const objIndex = updatedEvidence.findIndex(obj => obj[name] !== undefined);
      if (objIndex !== -1) {
        updatedEvidence[objIndex] = {
          ...updatedEvidence[objIndex],
          [name]: e.target.files[0]
        };
      }
      return updatedEvidence;
    });
  }

  // console.log('evidence list => ', evidence)

  const validationSchema = yup.object({
    forgeryReason: yup.string().required('Select any reason'),
    // evidence1: yup.mixed().required('Please upload the document as a proof')
  })

  const formik = useFormik({
    initialValues: {
      forgeryReason: '',
      otherReason: '',
    },

    onSubmit: (values) => {
      // console.log("getting forgery values => ", values)
      submitForm(values)
    }
    , validationSchema

  })

  function dataURLtoFile(dataurl, filename) {
    const arr = dataurl.split(",");
    const mime = arr[0].match(/:(.*?);/)[1];
    const bstr = atob(arr[1]);
    let n = bstr.length;
    const u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, { type: mime });
  }

  const submitForm = (values) => {

    if (ownerList?.length <= 0) {
      toast.info("Add atleast one owner...")
      return true;
    }

    evidence?.forEach((obj, index) => {
      if (obj?.docCode == '' || obj?.docName == '') {
        toast.info("Upload the document(s)...")
        return true;
      }
    })

    console.log("Entering in sumbission form => ", values, "\n and Owner List => ", ownerList)
    setisLoader(true)

    let fd = new FormData();
    fd.append('objectionFor', 'Forgery')
    fd.append('propId', propertyData?.id)
    fd.append('ulbId', propertyData?.ulb_id)
    fd.append("forgeryTypeMstrId", values.forgeryReason)
    { values.otherReason != '' && fd.append("remarks", values.otherReason) }
    evidence?.length > 0 && evidence?.forEach((obj, index) => {
      fd.append(`documents[${index}][doc]`, obj?.docName)
      fd.append(`documents[${index}][docCode]`, obj?.docCode)
    })
    ownerList?.length > 0 && ownerList?.forEach((obj, index) => {
      fd.append(`owners[${index}][ownerId]`, obj?.ownerId);
      fd.append(`owners[${index}][ownerName]`, obj?.ownerName);
      fd.append(`owners[${index}][gender]`, obj?.gender);
      fd.append(`owners[${index}][dob]`, obj?.dob);
      fd.append(`owners[${index}][guardianName]`, obj?.guardianName);
      fd.append(`owners[${index}][relation]`, obj?.relation);
      fd.append(`owners[${index}][mobileNo]`, obj?.mobileNo);
      fd.append(`owners[${index}][aadhar]`, obj?.aadhar);
      fd.append(`owners[${index}][pan]`, obj?.pan);
      fd.append(`owners[${index}][email]`, obj?.email);
      fd.append(`owners[${index}][isArmedForce]`, obj?.isArmedForce);
      fd.append(`owners[${index}][isSpeciallyAbled]`, obj?.isSpeciallyAbled);
      fd.append(`owners[${index}][ownerDoc]`, obj?.ownerDocument == '' ? '' : dataURLtoFile(obj?.ownerDocument, "IdProof.jpg"))
    });

    axios.post(postForgery, fd, ApiHeader())
      .then((res) => {
        console.log("Forgery Submitted", res)
        if (res?.data?.status == false) {
          activateBottomErrorCard(true, 'Error occurred in submitting objection application. Please try again later.')
        }
        setisLoader(false)
      })
      .catch((err) => {
        console.log("Forgery submission error  => ", err)
        activateBottomErrorCard(true, 'Error occurred in submitting objection application. Please try again later.')

        setisLoader(false)
      })

  }

  const activateBottomErrorCard = (state, msg) => {
    seterroMessage(msg)
    seterroState(state)

  }

  // ==============Owner function===============
  const [ownerList, setOwnerList] = useState([])
  const [ownerPreviewList, setownerPreviewList] = useState([])
  const [ownerPreviewForm, setownerPreviewForm] = useState({ isArmedForce: '0', isSpeciallyAbled: '0' })
  const [editStatus, setEditStatus] = useState(false) //to check edit or add of form
  const [editIndex, setEditIndex] = useState() //to carry the index to edit if edistatus is true
  const [AddOwnerForm, setAddOwnerForm] = useState('hidden') //to hide and show ownerform with animation
  const [loader, setloader] = useState(false)
  const [docCode, setdocCode] = useState(null)
  const [document, setdocument] = useState(null)
  const [nameDocList, setnameDocList] = useState()

  const { notify } = useContext(contextVar)

  const { clerical_add_member, getClericalDocCode } = apiLinks()

  useEffect(() => {

    propertyData?.owners?.map((data) => {
      let owner = {
        ownerId: data?.id,
        aadhar: data?.aadhar_no,
        dob: data?.dob,
        email: data?.email,
        gender: data?.gender,
        guardianName: data?.guardian_name,
        isArmedForce: data?.is_armed_force ? 'Yes' : 'No',
        isSpeciallyAbled: data?.is_specially_abled ? 'Yes' : 'No',
        mobileNo: data?.mobile_no,
        ownerDoc: '',
        ownerDocument: '',
        ownerName: data?.owner_name,
        pan: data?.pan_no,
        relation: data?.relation_type,
      }

      setOwnerList([...ownerList, owner])
      setownerPreviewList([...ownerPreviewList, owner])

    })

  }, [propertyData?.owners])

  // console.log('current date at owner ', getCurrentDate())

  // useEffect(() => {
  //   setownerPreviewList(propertyData?.owners)
  // }, [propertyData?.owners])

  const formik2 = useFormik({
    initialValues: {
      ownerId: '',
      ownerName: '',
      gender: '',
      dob: getCurrentDate(),
      guardianName: '',
      relation: '',
      mobileNo: '',
      aadhar: '',
      pan: '',
      email: '',
      isArmedForce: '0',
      isSpeciallyAbled: '0',
      ownerDoc: '',
      ownerDocument: ''
    },

    onSubmit: (values, resetForm) => {

      console.log('getting owner values => ', values)

      toggleForm()

      if (editStatus) {
        editOwnerList(values)
        return
      }
      //check for duplicate entry for 1-mobileNo 2-aadhar 3-pan
      let duplicateStatus = checkDuplicateOwner(values)
      if (duplicateStatus) {
        return
      }

      let tempOwnerList = [...ownerList, values] //taking copy of array adding latest values since setstate does not update immediatly
      setOwnerList([...ownerList, values])

      //* Adding ownerpreviewlist to preview data
      let tempOwnerPreviewList = [...ownerPreviewList, values] //taking copy of array adding latest values since setstate does not update immediatly
      setownerPreviewList([...ownerPreviewList, ownerPreviewForm])

      // collectFormDataFun('ownerDetails', tempOwnerList, tempOwnerPreviewList) //sending OwnerDetails data to parent to store all form data at one container
    }
    , validationSchema: yup.object({
      ownerName: yup.string().required('Enter owner name').max(50, 'Enter maximum 50 characters'),
      gender: yup.string().required('Select gender'),
      dob: yup.date().required('Select DOB'),
      guardianName: yup.string().required('Enter guardian name'),
      relation: yup.string().required('Select relation'),
      mobileNo: yup.string().required('Enter mobile no.').min(10, 'Enter 10 digit mobilen no'),
      aadhar: yup.string().required('Enter aadhar'),
      pan: yup.string().required('Enter pan'),
      email: yup.string().required('Enter email'),
      isArmedForce: yup.string().required('Select armed force status'),
      isSpeciallyAbled: yup.string().required('Select specially-abled status'),
      ownerDoc: yup.mixed().required('Upload id proof'),

    })
  })

  const checkDuplicateOwner = (currentOwner) => {
    let duplicateStatus = false
    let message = ''
    // console.log('at duplicate.....')
    ownerList.some((owner) => {
      if (currentOwner.aadhar != '' && (currentOwner.aadhar == owner.aadhar)) {
        duplicateStatus = true
        message = 'Duplicate aadhar no.'
        return
      }
      if (currentOwner.pan != '' && (currentOwner.pan == owner.pan)) {
        duplicateStatus = true
        message = 'Duplicate PAN no.'
        return
      }

    })

    { duplicateStatus && notify(message, 'error') } //notify toast if duplicate true with message
    // console.log('duplicate check....', duplicateStatus)

    return duplicateStatus
  }
  const editOwnerList = () => {
    let tempOwnerlist = [...ownerList]  //*copying the array
    tempOwnerlist[editIndex] = formik.values  //*updating value of editindex

    let tempOwnerPreviewList = [...ownerPreviewList]  //*copying the preview array
    tempOwnerPreviewList[editIndex] = ownerPreviewForm  //*updating value of preiview list

    // console.log('ownerDetails', tempOwnerlist, tempOwnerPreviewList) //*sending OwnerDetails data to parent to store all form data at one container

    setOwnerList(tempOwnerlist) //*setting value in origin ownlist array
    setownerPreviewList(tempOwnerPreviewList) //resetting the preview array


    setEditStatus(false) //*seting edit status false after successfull edit
    toggleForm()
  }

  const toggleForm = () => {
    if (AddOwnerForm === 'translate-y-0 top-[0px]') {
      setAddOwnerForm('hidden')
    } else {
      setAddOwnerForm('translate-y-0 top-[0px]')
    }
  }

  //funtion to remove owner from ownerlist via index
  const removeOwner = (index) => {
    setOwnerList(current =>
      current.filter((ct, cIndex) => {
        return cIndex != index
      }),
    );

    //removing owner for preview data
    setownerPreviewList(current =>
      current.filter((ct, cIndex) => {
        return cIndex != index
      }),
    );

  }


  // console.log('owner list.....', ownerList)
  //function to edit
  const editOwner = (index) => {
    setEditStatus(true)
    setEditIndex(index)
    let tempOwnerlist = [...ownerList]
    formik.resetForm()

    formik.initialValues.ownerName = tempOwnerlist[index].ownerName
    formik.initialValues.gender = tempOwnerlist[index].gender
    formik.initialValues.dob = tempOwnerlist[index].dob
    formik.initialValues.guardianName = tempOwnerlist[index].guardianName
    formik.initialValues.relation = tempOwnerlist[index].relation
    formik.initialValues.mobileNo = tempOwnerlist[index].mobileNo
    formik.initialValues.aadhar = tempOwnerlist[index].aadhar
    formik.initialValues.pan = tempOwnerlist[index].pan
    formik.initialValues.email = tempOwnerlist[index].email
    formik.initialValues.isArmedForce = tempOwnerlist[index].isArmedForce
    formik.initialValues.isSpeciallyAbled = tempOwnerlist[index].isSpeciallyAbled

    toggleForm()
  }
  const checkMinimumOwner = () => {
    if (ownerList.length === 0) {
      toast.error('Add minimum one owner')
    } else {
      toast.success('Owner list is complete')
    }
  }

  // console.log("gettting id => ", props?.id)


  const handleChange2 = (e) => {
    let name = e.target.name
    let value = e.target.value

    //input restrict validation
    { name == 'ownerName' && formik2.setFieldValue("ownerName", allowCharacterSpaceCommaInput(value, formik.values.ownerName, 100)) }
    { name == 'guardianName' && formik2.setFieldValue("guardianName", allowCharacterSpaceCommaInput(value, formik.values.guardianName, 100)) }
    { name == 'mobileNo' && formik2.setFieldValue("mobileNo", allowNumberInput(value, formik.values.mobileNo, 10)) }
    { name == 'aadhar' && formik2.setFieldValue("aadhar", allowNumberInput(value, formik.values.aadhar, 12)) }
    { name == 'pan' && formik2.setFieldValue("pan", allowCharacterNumberInput(value, formik.values.pan, 10)) }
    { name == 'email' && formik2.setFieldValue("email", allowMailInput(value, formik.values.email, 100)) }

    // { name == 'ownerDoc' && formik.setFieldValue('ownerDoc', JSON.stringify(e.target.files[0])) }

    if (name == 'ownerDoc') {
      const file = e.target.files[0];
      const reader = new FileReader();
      // formik2.setFieldValue('ownerDoc', e.target.files[0])
      reader.readAsDataURL(file);
      reader.onload = () => {
        const fileString = reader.result;
        console.log('document => ', fileString);
        formik2.setFieldValue('ownerDocument', fileString)
      };
    }

    //* Collecting owner details to preview
    if (e.target.type == 'select-one') {
      setownerPreviewForm({ ...ownerPreviewForm, [name]: e.target[e.target.selectedIndex].text })
    } else {
      setownerPreviewForm({ ...ownerPreviewForm, [name]: value })
    }

  }
  // console.log('owner preview list...', ownerPreviewList)

  let commonInputStyle = `form-control w-full px-3 text-xs 2xl:text-sm py-1 font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md w-full sm:w-max`;

  // ==============Owner function end===========

  return (
    <>

      <ToastContainer position="top-right" autoClose={2000} />


      {isLoader && <BarLoader />}
      {erroState && <BottomErrorCard activateBottomErrorCard={activateBottomErrorCard} errorTitle={erroMessage} />}

      {/* <div className='text-right relative top-0 animate__animated animate__fadeInDown'>
                <span className='bg-sky-100 border-l border-b border-white text-black col-span-12 sm:col-span-2 sm:col-start-11 pl-4 rounded-l shadow-lg font-semibold pr-4'><TbWebhook className='inline' /> Objection</span>
            </div> */}

      {/* <h1 className="mt-6 mb-2 font-serif font-semibold relative text-gray-600">
        <RiBuilding2Fill className="inline mr-2" />
       Forgery
      </h1> */}

      <div className="2xl:mt-6 mt-3 bg-indigo-500 text-white flex flex-row md:justify-evenly items-center justify-center uppercase text-base poppins mb-4 shadow-md py-2 rounded-md mx-6">
        <div className="flex items-center gap-2">
          <span className="font-extrabold text-[30px]">
            <FcDepartment />
          </span>
          <span className="font-semibold poppins 2xl:text-xl text-lg">
            Forgery Objection
          </span>
        </div>
      </div>

      <div className="poppins my-2 2xl:font-base text-sm px-6">
        Under Section 167 of the Jharkhand Municipal Act 2011, In this objection form citizen can file the following objections in Forgery case.
      </div>

      <form
        onSubmit={formik.handleSubmit}
        onChange={formik.handleChange}
        className="h-max p-4 mt-4 px-6 w-full"
      >
        {/* Title */}
        {/* <div className="flex flex-row justify-evenly items-center md:w-[20vw] w-[60vw] uppercase text-base">
          <span className='font-extrabold text-[30px]'><FcDepartment/></span>
          
          className='shadow-md rounded-md p-2 bg-white 2xl:text-base text-xs'>Forgery Objection</span>
        </div> */}

        {/* Correction Cards */}

        {/* <div className="mt-6 flex flex-wrap flex-col gap-y-4"> */}
        <div className="bg-white py-4 shadow-sm rounded-md transition-all duration-300 flex flex-wrap flex-col gap-y-4 px-2 w-full">
          {/* Name Card */}
          <div className="bg-zinc-50 rounded-md shadow-sm">
            <div className="px-4 py-1.5 text-gray-800 font-semibold uppercase poppins tracking-wider 2xl:text-base text-sm">

              Select Reasons for Forgery
            </div>
            <hr className='h-0 border-1 border-gray-300' />
            <div className="text-sm px-4 py-1.5 grid grid-cols-12">

              {
                forgeryReason?.map((elem, index) => <>
                  <div className='flex flex-row space-x-2 items-center py-2  col-span-12'>
                    <label className='flex flex-row space-x-2 items-center'>
                      <input type="radio" name="forgeryReason" className='rounded-md ' onChange={(e) => getDocCode(e.target.value)} value={elem?.id} />
                      <p className='2xl:text-sm text-xs poppins'>{elem?.type}</p></label>
                  </div>
                </>)
              }

            </div>
          </div>

          {/* Other Reason */}
          <div className="bg-white py-4 shadow-sm rounded-md transition-all duration-300 flex flex-wrap flex-col gap-y-4 px-2">
            {/* Name Card */}
            <div className="bg-zinc-50 rounded-md shadow-sm">
              <div className="px-4 py-1.5 text-gray-800 font-semibold uppercase poppins tracking-wider 2xl:text-base text-sm">

                Elaborate your Reason
              </div>
              <hr className='h-0 border-1 border-gray-300' />
              <div className="text-sm px-4 py-1.5 w-full">
                <div className='flex flex-row flex-1 space-x-2 items-center py-2  col-span-12'>
                  <textarea type="text" name="otherReason" value={formik.values.otherReason} placeholder='Elaborate your reason...' className={inputStyle} rows={2} />

                </div>
              </div>
            </div>
          </div>

          {/* ============Owner Details================ */}

          <div className="bg-zinc-50 rounded-md shadow-sm relative">
            <div className="px-4 py-1.5 text-gray-800 font-semibold uppercase poppins tracking-wider 2xl:text-base text-sm flex justify-between items-center">

              <span> Property Owner(s)</span>
              <button onClick={toggleForm} type="button" className=" px-3 py-1.5 2xl:px-6 2xl:py-2.5 bg-gray-200 text-black font-medium text-xs leading-tight uppercase rounded shadow-md hover:text-white hover:bg-gray-700 hover:shadow-lg focus:bg-gray-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-gray-800 active:shadow-lg transition duration-150 ease-in-out">Add Owner <BiAddToQueue className=' hidden md:inline font-semibold text-sm md:text-lg' /></button>
            </div>
            <p className='px-4'>Manage this property owner(s) according to your objection.</p>
            <hr className='h-0 border-1 border-gray-300' />
            <div className="text-sm px-4 py-1.5 w-full relative">

              <div className={`${AddOwnerForm} transition-all block px-4 w-full 2xl:py-6 rounded-lg shadow-lg  md:w-full absolute top-4 -left-2 bg-white z-50`}>
                <button onClick={toggleForm}><TiDelete className='absolute top-2 right-2 text-red-500 text-3xl hover:scale-125' /></button>
                <form onChange={handleChange2}>
                  <div className="grid grid-cols-1 md:grid-cols-5">
                    <div className={`grid col-span-5 grid-cols-1 md:grid-cols-5`}>

                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold "><small className="block mt-1 text-sm font-semibold text-red-600 inline poppins">*</small>Owner Name</label>
                        <input {...formik2.getFieldProps('ownerName')} type="text" className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md"
                          aria-describedby="emailHelp" placeholder="Enter owner name" />
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.ownerName && formik2.errors.ownerName ? formik2.errors.ownerName : null}</span>
                      </div>

                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold"><small className="block mt-1 text-sm font-semibold text-red-600 inline poppins">*</small>Gender</label>
                        <select {...formik2.getFieldProps('gender')} className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none cursor-pointer shadow-md" >
                          <option value="" disabled selected>select gender</option>
                          <option value="1">Male</option>
                          <option value="2">Female</option>
                          <option value="3">Other</option>
                        </select>
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.gender && formik2.errors.gender ? formik2.errors.gender : null}</span>
                      </div>
                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold"><small className="block mt-1 text-sm font-semibold text-red-600 inline poppins">*</small>DOB</label>
                        <input {...formik2.getFieldProps('dob')} type="date" className="block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none cursor-pointer shadow-md" />
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.dob && formik2.errors.dob ? formik2.errors.dob : null}</span>
                      </div>
                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold">Guardian Name</label>
                        <input {...formik2.getFieldProps('guardianName')} type="text" className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md"
                          placeholder="Enter guardian name" />
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.guardianName && formik2.errors.guardianName ? formik2.errors.guardianName : null}</span>
                      </div>
                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold">Relation</label>
                        <select {...formik2.getFieldProps('relation')} className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none cursor-pointer shadow-md" >
                          <option value="" disabled selected>select relation</option>
                          <option value="S/O">S/O</option>
                          <option value="D/O">D/O</option>
                          <option value="W/O">W/O</option>
                          <option value="C/O">C/O</option>

                        </select>
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.relation && formik2.errors.relation ? formik2.errors.relation : null}</span>
                      </div>
                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold"><small className="block mt-1 text-sm font-semibold text-red-600 inline poppins">*</small>Mobile No.</label>
                        <input {...formik2.getFieldProps('mobileNo')} type="text" className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md" placeholder='Enter mobile no' />
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.mobileNo && formik2.errors.mobileNo ? formik2.errors.mobileNo : null}</span>
                      </div>
                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold">Aadhar No</label>
                        <input {...formik2.getFieldProps('aadhar')} type="text" className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md"
                          placeholder="Enter aadhar no." />
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.aadhar && formik2.errors.aadhar ? formik2.errors.aadhar : null}</span>
                      </div>
                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold">PAN No.</label>
                        <input {...formik2.getFieldProps('pan')} type="text" className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md"
                          placeholder="Enter pan no." />
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.pan && formik2.errors.pan ? formik2.errors.pan : null}</span>
                      </div>
                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold">email</label>
                        <input {...formik2.getFieldProps('email')} type="email" className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md"
                          placeholder="Enter email." />
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.email && formik2.errors.email ? formik2.errors.email : null}</span>
                      </div>
                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold"><small className="block mt-1 text-sm font-semibold text-red-600 inline poppins">*</small>Is-Armed-Force</label>
                        <select {...formik2.getFieldProps('isArmedForce')} className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md" >
                          <option value=''>Select</option>
                          <option value='0'>No</option>
                          <option value='1'>Yes</option>
                        </select>
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.isArmedForce && formik2.errors.isArmedForce ? formik2.errors.isArmedForce : null}</span>
                      </div>

                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold"><small className="block mt-1 text-sm font-semibold text-red-600 inline poppins">*</small>Is-Specially-Abled?</label>
                        <select {...formik2.getFieldProps('isSpeciallyAbled')} className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none cursor-pointer shadow-md" >
                          <option value=''>Select</option>
                          <option value='0'>No</option>
                          <option value='1'>Yes</option>
                        </select>
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.isSpeciallyAbled && formik2.errors.isSpeciallyAbled ? formik2.errors.isSpeciallyAbled : null}</span>
                      </div>

                      <div className="form-group col-span-4 md:col-span-1 mb-2 md:px-4">
                        <label className="form-label poppins inline-block mb-1 text-gray-600 2xl:text-sm text-xs font-semibold"><small className="block mt-1 text-sm font-semibold text-red-600 inline poppins">*</small>Id Proof</label>
                        <input {...formik2.getFieldProps('ownerDoc')} className="form-control poppins block w-full px-3 py-1.5 text-base md:text-xs font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none cursor-pointer shadow-md" type='file' accept='.jpg, .jpeg, .pdf' />
                        <span className="text-red-600 absolute text-xs poppins">{formik2.touched.ownerDoc && formik2.errors.ownerDoc ? formik2.errors.ownerDoc : null}</span>
                      </div>

                      <div className=" flex justify-center items-end">
                        <div className='md:px-10'>
                          <div onClick={formik2.handleSubmit} className="poppins 2xl:px-6 px-3 2xl:py-2.5 py-1.5 bg-blue-600 text-white font-medium text-xs leading-tight  rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out cursor-pointer">{editStatus ? 'Update' : 'Add'}</div>
                        </div>
                      </div>

                    </div>
                  </div>
                </form>
              </div>

              <div className={`${AddOwnerForm == 'translate-y-0 top-[200px]' ? 'hidden' : 'block'}    mt-2  md:py-4 md:px-0 md:pb-0 md:pt-0 rounded-lg  mx-auto w-full overflow-x-scroll`}>

                {ownerPreviewList?.length != 0 && <table className='min-w-full leading-normal mt-4 2xl:mt-0'>
                  <thead className='font-semibold text-left text-sm bg-sky-50'>
                    <tr>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">#</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Owner</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Gender</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">DOB</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Guardian</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Relation</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Mobile No</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Aadhar</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">PAN</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Email</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Is Armed</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Is Specially</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Id Proof</th>
                      <th className="px-2 py-3 border-b border-gray-200 text-gray-800  poppins text-xs  text-left">Action</th>
                    </tr>
                  </thead>
                  <tbody className="text-sm">
                    {
                      ownerPreviewList?.map((data, index) => (
                        <>
                          <tr className="bg-white shadow-lg border-b border-gray-200">
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{index + 1}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.ownerName)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.gender)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.dob)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.guardianName)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.relation)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.mobileNo)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.aadhar)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.pan)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{nullToNA(data?.email)}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{data?.isArmedForce}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{data?.isSpeciallyAbled}</td>
                            <td className="px-2 py-2 2xl:text-sm text-xs poppins text-left">{data?.ownerDoc != '' ? <span className='text-2xl'><FcDocument /></span> : "NA"}</td>
                            <td className="px-2 py-2 text-sm text-left">{!data?.ownerId && <TbEdit onClick={() => editOwner(index)} className='inline text-green-500 font-semibold text-lg cursor-pointer hover:text-green-700 relative hover:scale-150' />}<RiDeleteBack2Line onClick={() => removeOwner(index)} className='inline ml-2 text-red-400 font-semibold text-lg cursor-pointer hover:text-red-700 relative hover:scale-150' /></td>
                          </tr>
                        </>
                      ))
                    }
                  </tbody>
                </table>}
                {/* <div className=' '> */}
                <div className=' my-1 bg-red-50 text-red-400 px-2 py-2 rounded-sm  opacity-80 2xl:mt-10 mt-5 text-sm w-full'>
                  <AiFillInfoCircle className="inline mr-2" />
                  Click <b>Add Owner</b> button to add owner of the property, You can add multiple owners by repeating the same method
                </div>
                {/* </div> */}
              </div>


            </div>
          </div>

          {/* =============Owner details end here=============== */}

          {/* Evidence Documnent */}
          <div className="bg-zinc-50 rounded-md shadow-sm relative">
            <div className="px-4 py-1.5 text-gray-800 font-semibold uppercase poppins tracking-wider 2xl:text-base text-sm">

              Upload Evidence for Forgery
            </div>
            <hr className='h-0 border-1 border-gray-300' />
            <div className="text-sm px-4 py-1.5 grid grid-cols-12">

              {documentForgery?.map((elem) =>
                <div className="shadow-md col-span-12 grid grid-cols-12 text-sm px-2 sm:px-6 py-2 mt-2 my-2 gap-y-2 font-base poppins bg-zinc-50 rounded-sm mb-2">
                  <div className="col-span-12 md:col-span-4 poppins 2xls:text-base md:text-sm text-xs">
                    {elem?.docName == '' ? 'Evidence' : elem?.docName} :
                    <span className="font-semibold 2xls:text-base md:text-sm text-sm poppins">

                    </span>
                  </div>
                  <div className="col-span-12 md:col-span-4 poppins  2xls:text-base md:text-sm text-xs">
                    <span className="poppins">Select Document :</span>{" "}
                    <br />
                    <span className='w-full'>
                      <select
                        name="docCode"
                        onChange={(e) => documentCodeChange(e, elem?.docName)}
                        className={
                          commonInputStyle +
                          ` poppins  2xls:text-base md:text-sm text-xs w-full`
                        }
                      >
                        <option value={elem?.masters?.length == 0 ? 'other' : ''} selected>
                          {elem?.masters?.length == 0 ? 'Other' : 'Select'}
                        </option>

                        {elem?.masters?.length > 0 &&
                          elem?.masters?.map((item) => (
                            <>
                              <option
                                value={item?.documentCode}
                                className="poppins"
                              >
                                {item?.docVal}
                              </option>
                            </>
                          ))
                        }

                      </select>
                    </span>
                  </div>
                  <div className="col-span-12 md:col-span-4 poppins flex flex-col  2xls:text-base md:text-sm text-xs">
                    Upload Document : <br />
                    <span>
                      <input
                        type="file"
                        onChange={(e) => documentChange(e, elem?.docName)}
                        className={commonInputStyle + ` poppins `}
                        accept=".pdf,.jpg,.jpeg"
                        name="document"
                        id=""
                      />
                    </span>
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* =========Note======== */}
          <div className="text-xs text-red-600 col-span-9 italic mx-4 my-0">

            Note :
            Only .pdf , .jpg and .jpeg file accepted..
          </div>

        </div>


        {/* Buttons */}
        <div className="flex flex-row flex-wrap justify-between items-center mt-4">
          <div
            onClick={props.closePopUp}
            className="cursor-pointer px-3 py-1.5 2xl:px-6 2xl:py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight  rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-dotted focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out poppins"
          >
            Close
          </div>
          <div>
            <button
              type="submit"
              className=" px-3 py-1.5 2xl:px-6 2xl:py-2.5 bg-green-600 text-white font-medium text-xs leading-tight poppins rounded shadow-md hover:bg-green-700 hover:shadow-lg focus:bg-green-700 focus:shadow-lg focus:outline-dotted focus:ring-0 active:bg-green-800 active:shadow-lg transition duration-150 ease-in-out"
            >

              Submit
            </button>
          </div>
        </div>
      </form>

    </>
  )
}

export default ObjectionForgery